$regular: 'Graphik LCG - Regular';
$bold: 'Graphik LCG - Bold';
$medium: 'Graphik LCG - Medium';
$headerHeight: calc(80px + 88px);

* {
  outline: none;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-smoothing: antialiased;
  &::before, &::after {
    outline: none;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-smoothing: antialiased; } }

body, html {
  overflow-x: hidden !important; }

h1, h2, h3, ul, p, body {
  padding: 0 !important;
  margin: 0 !important; }

a {
  text-decoration: none !important; }

ul {
  list-style: none; }

img {
  -moz-user-select: none;
  user-select: none;
  pointer-events: none; }

.navbar {
  min-height: 88px;
  &__mobileBtn {
    min-height: 50px !important; }
  &__mobileLink {
    font-size: 21px;
    color: #9b9b9b;
    &:first-child {
      margin-right: 15px; } }
  &__mobileSearch {
    margin: 0;
    background-color: #fff; }
  &__searchInput {
    width: 100%;
    padding-right: 100px !important; }
  &__searchBtn {
    width: 100px;
    color: #4b6ecd;
    cursor: pointer;
    border: none;
    background-color: transparent; }
  &__logo {
    margin-right: 60px; }
  &__logoImg {
    width: 100px; }
  &__logoLink {
    display: block; }
  &__phone {
    display: flex;
    flex-direction: column; }
  &__phoneText {
    color: #000;
    font-size: 15px;
    font-family: $medium;
    transition: .2s;
    &:hover {
      color: #2a50b6; } }
  &__phoneCity {
    color: #202020;
    font-family: $regular;
    font-size: 14px; }
  &__navLink {
    min-height: unset !important;
    padding: 0 !important;
    text-transform: unset !important;
    font-size: 15px !important;
    transition: .2s !important;
    color: #565656 !important;
    &:hover {
      color: #2a50b6 !important; } }
  &__navLi {
    margin-left: 58px;
    &:first {
      margin-right: 0; } } }

.QueryTitle {
  height: 80px;
  display: flex;
  align-items: center;
  &__text {
    font-family: $bold;
    font-size: 35px; } }

.AsideBlock {
  border-bottom: 1px solid #eee;
  padding: 20px;
  &__accordeonTitle {
    font-size: 18px !important;
    font-family: $medium !important;
    position: relative !important;
    color: #000 !important;
    overflow: visible !important;
    &::after {
      content: '';
      display: block;
      position: absolute;
      left: -20px;
      top: 2px;
      width: 2px;
      background-color: #9b9b9b;
      height: 20px; } }
  &__inputText {
    color: #9b9b9b;
    font-size: 13px;
    font-family: $regular; }
  &__input {
    height: 35px !important;
    background: #fff !important;
    border: 1px solid #eee !important;
    border-radius: 2px !important;
    font-size: 14px !important;
    font-family: $regular !important; }
  &__sliderBlock {
    margin-top: 18px;
    padding: 0 14px; }
  &__sliderPrice {
    border: none !important;
    border-radius: 10px !important;
    background-color: #eee !important;
    height: 10px !important; }
  &__checkboxesBlock {
    margin-top: 20px; }
  &__checkboxBlock {
    margin-bottom: 10px;
    &:last-child {
      margin-bottom: 0; } }
  &__checkboxInput {
    display: none; }
  &__checkboxLabel {
    cursor: pointer;
    font-size: 14px;
    color: #000;
    position: relative;
    padding-left: 30px;
    &:hover::before {
      border-color: #9b9b9b;
      background-color: #f6f6f6; }
    &::before {
      border-radius: 2px;
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      width: 20px;
      height: 20px;
      border: 1px solid #d8d8d8;
      background: #fff; } }
  &__checkboxInputActive {
    &:hover::before {
      border-color: #4b6ecd;
      background-color: #4b6ecd; }
    &::before {
      border-color: #4b6ecd;
      background-color: #4b6ecd;
      font-family: 'icomoon' !important;
      speak: never;
      font-style: normal;
      font-weight: normal;
      font-variant: normal;
      text-transform: none;
      line-height: 1;
      -webkit-font-smoothing: antialiased;
      -moz-osx-font-smoothing: grayscale;
      content: '\e900';
      color: #fff; } }
  &__searchInput {
    height: 35px !important;
    border: 1px solid #eee !important;
    border-radius: 2px !important;
    color: #202020 !important;
    font-size: 13px !important;
    font-family: $medium !important; }
  &__searchBtn {
    border: none;
    background-color: transparent;
    cursor: pointer; }
  &__searchBlock {
    width: 100%; }
  &__scrollContent {
    margin-top: 17px;
    max-height: 215px;
    overflow: hidden;
    overflow-y: scroll; } }
.ui-slider-handle {
  border-radius: 50% !important;
  width: 20px !important;
  height: 20px !important;
  background: #fff !important;
  border: none !important;
  cursor: pointer !important;
  box-shadow: 0 2px 5px 0 rgba(0,0,0,.3) !important; }
.ui-slider-range {
  height: 10px !important;
  background: #9b9b9b !important; }

.ProductSlider {
  display: flex;
  box-sizing: border-box;
  padding: 10px;
  background: #fff;
  box-shadow: 0 1px 2px 0 rgba(0,0,0,.2),0 0 1px 0 rgba(0,0,0,.15);
  border-radius: 8px;
  transition: .2s;
  align-items: center;
  &__image {
    min-width: 100px;
    width: 100px;
    height: 100px; }
  &__title {
    margin-left: 20px; }
  &__name {
    display: flex;
    color: #565656;
    font-size: 13px;
    font-family: $regular;
    transition: .2s;
    margin-bottom: 25px;
    &:hover {
      color: #4b6ecd; } }
  &:hover {
    box-shadow: 0 1px 3px 0 rgba(0,0,0,.08),0 5px 8px 0 rgba(0,0,0,.05); }
  &__price {
    color: #000;
    font-family: $medium;
    font-size: 18px; }
  &__link {
    display: flex; } }

.MiniBtn {
  display: flex !important;
  justify-content: center !important;
  align-items: center !important;
  font-size: 13px !important;
  padding: 0 10px !important;
  color: #9b9b9b !important;
  background-color: #fff !important;
  border-color: #eee !important;
  overflow: hidden !important;
  height: 35px !important;
  border-radius: 2px !important;
  font-family: $medium !important;
  margin: 0 5px 5px 0 !important;
  transition: .2s !important;
  &:hover {
    border-color: #9b9b9b !important; } }

.Product {
  position: relative;
  padding: 25px;
  height: 540px;
  box-sizing: border-box;
  background: #fff;
  border-radius: 8px;
  box-shadow: none;
  transition: .2s;
  &__image {
    max-height: 215px;
    max-width: 215px; }
  &__price {
    margin-top: 30px;
    margin-bottom: 20px;
    height: 50px; }
  &__priceText {
    font-family: $bold;
    font-size: 18px;
    color: #000; }
  &__priceFrom, &__merchantPrice {
    font-family: $regular;
    font-size: 12px;
    color: #9b9b9b; }
  &__name {
    display: inline-block;
    color: #202020;
    font-family: $medium;
    font-size: 13px;
    transition: .2s;
    line-height: 1.3;
    margin-bottom: 14px !important;
    &:hover {
      color: #4b6ecd; } }
  &__star, &__unstar {
    font-size: 12px; }
  &__star {
    color: #ff9935; }
  &__unstar {
    color: #d8d8d8; }
  &__descriptionText {
    color: #9b9b9b;
    font-size: 11px;
    margin-bottom: 6px !important;
    font-family: $regular; }
  &__descriptionCustom {
    color: #000; }
  &__footer {
    position: absolute;
    bottom: 20px;
    left: 0;
    padding: 0 20px;
    box-sizing: border-box;
    display: flex;
    width: 100%;
    justify-content: space-between;
    align-items: center; }
  &__btn {
    display: flex !important;
    justify-content: center !important;
    align-items: center !important;
    height: 35px !important;
    padding: 0 25px !important;
    border: 1px solid #eee !important;
    background-color: #fff !important;
    color: #9b9b9b !important;
    border-radius: 4px !important;
    font-family: $medium !important;
    transition: .2s !important; }
  &__today {
    text-align: center;
    font-size: 12px;
    font-family: $medium;
    transition: .2s;
    text-transform: uppercase;
    max-width: 100%;
    width: 50%;
    color: #9b9b9b; }
  &__badge {
    position: absolute;
    left: 12px;
    top: 12px; }
  &__badgeImg {
    max-height: 48px;
    max-width: 48px; }
  &__merchant {
    position: absolute;
    width: 70px;
    height: 70px;
    background-image: url('../img/tag-with-rope.svg');
    background-size: 70px;
    background-repeat: no-repeat;
    position: absolute;
    right: -8px;
    top: 180px; }
  &__merchantPercent {
    position: absolute;
    top: 30px;
    left: 14px;
    transform: rotate(-25.43deg);
    font-size: 16px;
    font-family: $bold;
    color: #fff; }
  &__merchantPriceSpec {
    position: relative;
    display: inline-block;
    &::after {
      content: '';
      display: block;
      position: absolute;
      left: 0;
      top: 45%;
      transform: rotate(-9deg);
      width: 100%;
      height: 2px;
      background-color: rgba(255,90,90,.8);
      border-radius: 2px; } }
  &__merchantInfo {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    margin: 0 4px;
    width: 12px;
    height: 12px;
    font-size: 9px;
    border: 1px solid #d8d8d8;
    border-radius: 50%;
    color: #d8d8d8;
    cursor: default; }
  &:hover {
    box-shadow: 0 1px 3px 0 rgba(0,0,0,.08),0 5px 8px 0 rgba(0,0,0,.05);
    .Product__btn {
      border-color: #4b6ecd !important;
      background-color: #4b6ecd !important;
      color: #fff !important;
      &:hover {
        background-color: #2a50b6 !important;
        border-color: #2a50b6 !important;
        color: #fff !important; } }
    .Product__today {
      color: #4b6ecd;
      &:hover {
        color: #2a50b6; } } } }

.ProductList {
  display: flex;
  position: relative;
  padding: 25px;
  box-sizing: border-box;
  background: #fff;
  border-radius: 8px;
  box-shadow: none;
  transition: .2s;
  margin-bottom: 10px;
  &__footer {
    position: absolute;
    top: 0;
    right: 20px;
    height: 100%;
    padding: 20px;
    box-sizing: border-box; }
  &__btn {
    position: absolute;
    bottom: 20px; }
  &__header {
    margin-right: 20px; }
  &__img {
    max-height: 157px;
    max-width: 157px; }
  &__priceText {
    font-family: $bold;
    font-size: 22px;
    color: #000; }
  &:hover {
    box-shadow: 0 1px 3px 0 rgba(0,0,0,.08),0 5px 8px 0 rgba(0,0,0,.05);
    .Product__btn {
      border-color: #4b6ecd !important;
      background-color: #4b6ecd !important;
      color: #fff !important;
      &:hover {
        background-color: #2a50b6 !important;
        border-color: #2a50b6 !important;
        color: #fff !important; } }
    .Product__today {
      color: #4b6ecd;
      &:hover {
        color: #2a50b6; } } } }

.SectionTitle {
  margin-bottom: 30px;
  &__text {
    font-size: 25px;
    font-family: $bold; } }
.SectionBody {
  display: flex;
  flex-wrap: wrap;
  &__queries {
    display: flex;
    flex-wrap: wrap; }
  &__slider {
    height: 200px;
    align-items: center; }
  &__sliderNav {
    top: -50px !important; }
  &__sliderNavPrev {
    display: inline-block;
    transform: rotate(-90deg); }
  &__sliderNavNext {
    display: inline-block;
    transform: rotate(90deg); }
  &__sliderButton {
    padding: 0 !important;
    height: 40px !important;
    width: 40px !important;
    display: flex !important;
    justify-content: center !important;
    align-items: center !important; } }
